* {
  box-sizing: border-box;
  text-decoration: none;
}

html,
body {
  font-family: "Roboto", sans-serif;
  height: 100vh;
  width: 100%;
}

header {
  position: relative;

  display: flex;
  flex-direction: row;
  justify-content: center;

  width: 100%;
  height: 72px;
  @media screen and (min-width: 1024px) {
    justify-content: start;
  }

  .logo {
    position: relative;
    img {
      width: 75px;
      padding: 10px 0;
      @media screen and (min-width: 1024px) {
        position: absolute;
        left: 15px;
      }
    }
  }

  .search-cart {
    display: flex;
  }

  .hamburger-menu {
    // position: absolute;
    position: relative;
    cursor: pointer;
    left: 15px;
    top: 13px;

    img {
      width: 50px;
    }

    @media screen and (min-width: 1024px) {
      display: none;
    }
  }

  .flex-nav {
    position: relative;
    // display: flex;

    &.showing {
      position: absolute;
      display: flex;
      justify-content: center;
      z-index: 2;
      width: 300px;
      height: 300px;
      left: 0;
      top: 70px;
      background: #fff;

      .hamburger-nav {
        display: flex;
        flex-direction: column;
        margin-top: 20px;
      }

      li {
        padding: 20px 0;
      }

      li a {
        color: #333;
        font-size: 1.5rem;
      }
    }
  }

  @media screen and (min-width: 1024px) {
    .flex-nav {
      position: relative;
      display: flex;
      align-items: center;
    }
  }

  ul {
    display: none;
    @media screen and (min-width: 1024px) {
      position: absolute;
      display: flex;
      left: 150px;

      li a {
        color: #333;
        padding-right: 25px;
        text-transform: uppercase;
      }
    }
  }
}

.search-cart {
  display: none;
  @media screen and (min-width: 768px) {
    position: absolute;
    display: flex;
    align-items: center;
    right: 15px;
    top: 22px;

    .search-field {
      padding-right: 25px;
    }

    .search-btn img {
      width: 25px;
      height: 25px;
      cursor: pointer;
    }

    .shopping-cart img {
      width: 25px;
      height: 25px;
      cursor: pointer;
    }
  }
}

footer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px 0;

  background-color: #333;

  span {
    color: #fff;
  }
}
